import React, { useState, useEffect } from 'react';

import ListSlider from './ListSlider'
import EmptyList from './EmptyList';

function ListsPage({ lists, listOrder, onDragEnd, allTasks, handleAddList, handleEditListTitle, handleDeleteList, handleEditTask, handleToggleDone, handleRemoveTask }) {

    const [searchInput, setSearchInput] = useState('');




  
    const handleSearchInput = e => {
      setSearchInput(e.target.value)
    //   console.log(searchInput)
    }
    
    
    let slider = document.querySelector('.slider');
    let innerSlider = document.querySelector('.slider-inner');
    let pressed = false;
    let startx;
    let x;


    const handleOnMouseDown = (e) => {
        pressed = true;
        startx = e.nativeEvent.offsetX - innerSlider.offsetLeft;
        // document.style.cursor = 'grabbing';
    }

    const handleMouseEnter = () => {
        // slider.style.cursor = 'grab';
    }

    const handleMouseLeave = () => {
        // slider.style.cursor = 'default'
    }

    const handleMouseUp = () => {
        pressed = false;
        // slider.style.cursor = 'grab';
    }

    const handleMouseMove = e => {
        if(!pressed) return;
        e.preventDefault();
    
        x = e.nativeEvent.offsetX

    innerSlider.style.left = `${x - startx}px`;
         checkBound()
    }

    function checkBound () {
        let outer = slider.getBoundingClientRect();
        let inner = innerSlider.getBoundingClientRect();
    
        if(parseInt(innerSlider.style.left) > 0) {
            innerSlider.style.left = '0px'
        } else if (inner.right < outer.right) {
            innerSlider.style.left = `-${inner.width - outer.width}px`
        }
    }

    return (
        <div 
            className="col-12"
            style={{height: "80vh",
                    width: "100%",
                    padding: 0,
                    minHeight: 800,
                    position: 'relative'
            }} 
            id="Lists"
        >
            
        {/* slider handle */}
        <div 
            className='w-100'
            style={{height: 500, position: 'absolute', top: 0}}
            onMouseDown={handleOnMouseDown}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            onMouseUp={handleMouseUp}
            onMouseMove={handleMouseMove}        
        ></div>

        <div 
            className="row pb-5"
            style={{paddingTop: "5.5rem"}}
        >
            <div className="col-lg-10 me-md-auto ps-md-3">
                
                {/* <SearchBar />   */}
                <div
                    className="mx-3 mx-sm-5 px-3 searchBar"
                    style={{
                            background: "white",
                            borderRadius: 19,
                            display: "flex"
                        }}>
                    <img className="d-inline-blcok mx-3 search-icon" src="data:image/svg+xml;utf8;base64," />
                    <input
                        id="search"
                        className="search bg-0" 
                        type="text" 
                        placeholder="search" 
                        style={{borderRadius: 19,border: 0, height: 40, display: "inline-flex", flexGrow: 1, zIndex: 99999}}
                        onChange={handleSearchInput}
                    />
                </div>

            </div>
            <div className="col-2">

            </div>
        </div>

        <div 
            className="row"
            style={{
                height: "80%",
                overflow: "hidden",
            }}
        >
            <div 
                className="col w-100"
                style={{overflow: "hidden"}}
            >
                {listOrder.length === 0 ?
                <EmptyList handleAddList={handleAddList} />
                :
                <ListSlider
                    lists={lists}
                    listOrder={listOrder}
                    onDragEnd={onDragEnd}
                    allTasks={allTasks}
                    handleAddList={handleAddList}
                    handleEditListTitle={handleEditListTitle}
                    handleDeleteList={handleDeleteList}

                    handleEditTask={handleEditTask}
                    handleToggleDone={handleToggleDone}
                    handleRemoveTask={handleRemoveTask}
                    searchInput={searchInput}

                />
                }



            </div>









        </div>
    </div>

    );
}

export default ListsPage;