import React from 'react';

import styled from 'styled-components';


const ResetButton = styled.div`
    opacity: 0;
    transition: 0.2s;
`

function SearchBar({ searchInput, searchItems, handleSearchInput }) {

    const handleSearch = e => {
        handleSearchInput(e.target.value)
    };

    const resetSearch = () => {
        handleSearchInput("")
    }
    return (
        <div className="px-2 search-bar searchBar">
            <img className="d-inline-blcok mx-3 search-icon searchBar" src="data:image/svg+xml;utf8;base64," />
            <input id="search" className="bg-0 search" type="text" placeholder="search" value={searchInput} onChange={handleSearch} />
            
            <ResetButton className="my-auto me-2" type="button" onClick={resetSearch} style={{ opacity: searchInput.length>=1 && 1}}>
                Ｘ
            </ResetButton>
            {/* {searchInput &&
            
            <button onClick={resetSearch}>x</button>
            } */}
        </div>
    );
}

export default SearchBar;